.nav{
    &__list{
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
        flex-direction: column;
        gap: 32px;
        text-transform: none;
    }

    &__link{
        position: relative;
        color: inherit;
        text-decoration: none;
        font-size: 22px;
        line-height: 22px;
        letter-spacing: 2px;

        &::after{
            content: '';
            position: absolute;
            display: block;
            bottom: -8px;
            height: 1px;
            width: 100%;
            background-color: $mainColor;
            transition: transform 0.3s;
            transform: scale(0);
            transform-origin: left;
        }

        &:hover::after{
            transform: scale(1);
        }
    }
}